import React from 'react';
import { Illustration } from 'react-zdog';
import { RotatingAnchor, ShapeExtrusion } from '../src';

const spacing = (i, v) => ({ x: 0, y: Math.sin(v * Math.PI * 2) * 20, z: i * 1.1 });
const sideColor = (v) => `rgb(${v * 255}, ${0}, ${0})`;

const Index = () => (
  <div style={{width: '800px', height: '800px'}}>
    <Illustration>
        <RotatingAnchor rotate={{ y: Math.PI / 2 }}>
            <ShapeExtrusion depth={100} spacing={spacing} sideColor={sideColor}/>
        </RotatingAnchor>
    </Illustration>
  </div>
);

export default Index;